.container {
  position: relative;

  max-width: 500px;
  width: 100%;
  background: #fff;
  margin: 50px auto;
  padding: 10px 20px;
  border-radius: 7px;
}

.title-text {
  text-align: center;
  background-color: crimson;
  color: #fff;
  font-weight: 400;
  border-radius: 50px;
  margin-top: 15px;
  margin-bottom: 30px;
}

.container .skill-box {
  width: 100%;
  margin: 25px 0;
}

.skill-box .title-skill {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #333;
  text-align: left;
}

.skill-box .skill-bar {
  height: 8px;
  width: 100%;
  border-radius: 6px;
  margin-top: 6px;
  background: rgba(0, 0, 0, 0.1);
}

.skill-box .skill-per {
  position: relative;
  display: block;
  height: 100%;
  width: 95%;
  border-radius: 6px;
  background: crimson;
  animation: progress 0.4s ease-in-out forwards;
  opacity: 0;
}

.skill-per.html {
  width: 95%;
  animation-delay: 0.1s;
}
.skill-per.css {
  width: 70%;
  animation-delay: 0.2s;
}

@keyframes progress {
  0% {
    width: 0;
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.skill-per .tooltip {
  position: absolute;
  right: -14px;
  top: -35px;
  font-size: 9px;
  font-weight: 500;
  color: #fff;
  padding: 2px 6px;
  border-radius: 3px;
  background: crimson;
  z-index: 1;
}

.tooltop::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -2px;
  height: 10px;
  width: 10px;
  z-index: -1;
  background-color: crimson;
  transform: translateX(-50%) rotate(45deg);
}
